.TodoButtomMain{
position: fixed;
display: flex;
justify-content: center;
z-index: 1;
bottom: 3vh;
    right: 3vh;

}

.TodoButtom{
    width: 56px;
    height: 56px;
    /* background: #646fd4; */
    background: #64c9d4;
    box-shadow: 1px 1px 4px rgba(0,0, 0, 0.25);
    border-radius: 56px;
    font-size: 30px;
    color: white;
    cursor: pointer;
    
}
.header{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    position: -webkit-sticky;
    position: sticky;
    top: 0;
    z-index: 999;
    background: #dbe8f3;
   
}
.TodoCounter-Title{
    text-align: center;
    margin-bottom: 5px;
}
.TodoCounter--loading{
    opacity: 25%;
}
.LoadingTodo-container {
  height: 50px;
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    box-shadow: 0px 5px 50px rgba(32, 35, 41, 0.15);
    border-radius: 15px;
    margin-bottom: 5px;
  }
  
  .LoadingTodo-text {
    padding-left:20%;
    margin: 24px 0 24px 24px;
    width: calc(100% - 120px);
    font-size: 18px;
    line-height: 24px;
    font-weight: 400;
  }
  
  .LoadingTodo-completeIcon,
  .LoadingTodo-deleteIcon {
    border-radius: 50px;
    height: 48px;
    width: 48px;
  }
  
  .LoadingTodo-completeIcon {
    position: absolute;
    left: 12px;
  }
  
  
  
  .LoadingTodo-container,
  .LoadingTodo-completeIcon,
  .LoadingTodo-deleteIcon {
    background: linear-gradient(90deg, rgba(250,250,250,1), rgb(200, 199, 199));
    background-size: 400% 400%;
    -webkit-animation: loadingAnimation 3s ease-in-out infinite;
            animation: loadingAnimation 3s ease-in-out infinite;
  }
  
  @-webkit-keyframes loadingAnimation {
    0% {
      background-position: 0% 50%;
    }
    50% {
      background-position: 100% 50%;
    }
    100% {
      background-position: 0% 50%;
    }
  }
  
  @keyframes loadingAnimation {
    0% {
      background-position: 0% 50%;
    }
    50% {
      background-position: 100% 50%;
    }
    100% {
      background-position: 0% 50%;
    }
  }
.TodoItem{
    position: relative;
    scroll-snap-align: end;
    width: 100%;
    margin-top: 5px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    flex-direction: row;
    border-radius: 5px;
    border: 1px solid #dcdce5;
    box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
    background-color: #f7f7f7c2;
}
/* .TodoItem--content{
    padding-right: 150px;
} */

.TodoItem-p{
    margin: 0px;
    font-size: 18px;
    line-height: 24px;
    font-weight: 500;
}

.TodoItem-date{
    margin: 0px;
    font-weight: 100;
}

.TodoItem-p--completed{
    text-decoration: line-through;
}

.Icon {
    cursor: pointer;
    display: flex;
    justify-content: center;
    align-items: center;
    height: 48px;
    width: 48px;
    font-size: 24px;
    font-weight: bold;
}

.Icon-check--active{
    color: #4caf50;
}

.Icon-delete:hover {
    color: red;
}

.TodoListMain{
    display: flex;
    justify-content: center;
    width: 100%;

}
.TodoList{
    display: flex;
    flex-direction: column;
    padding-left: 0px;
    overflow-y: scroll;
    width: 95%;
    

}
.TodoSearchMain{
    display: flex;
    flex-direction: row;
    justify-content: space-evenly;
    align-items: center;
    margin: 0 auto;
    width: 80%;
    height: 45px;
    background-color: white;
    border-radius: 15px;
    border: 1px solid #dcdce5;
    box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
    margin-bottom: 5px;


}

.TodoSearchInput{
    width: 80%;
    height: 85%;
    background-color: transparent;
    border: none;    
    outline: none;
}

.TodoSearchInput:disabled{
    opacity: 50%;
}
.form-container{
    margin-bottom: 5px;
    width: 90%;
    background: white;
    height: 80px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    border-radius: 5px;
    border: 0;
    padding-left: 10px;
    padding-right: 10px;
    box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
 
}
.textArea-container{
    display: flex;
    flex-direction: row;
    grid-gap: 5px;
    gap: 5px;
    
    align-items: center;
    border-bottom: 2px solid #ceced3;
}
.form-textArea{
    width: 100%;
border: 0;
outline: none;
}

.form-textArea::-webkit-input-placeholder{
    color: black;
    vertical-align:bottom;
    padding-top: 15px;
    
    
}

.form-textArea::placeholder{
    color: black;
    vertical-align:bottom;
    padding-top: 15px;
    
    
}

.calender-container{
    display: flex;
    flex-direction: row;
    grid-gap: 5px;
    gap: 5px;
    color: black;
    align-items: center;
    justify-content: space-between;

}

.calender{
    display:flex ;
    grid-gap:10px;
    gap:10px;
    align-items: center;
}

.Datepicker{
    color: white !important; 
}

